<template>
	<a href="javascript:;" @click.prevent="onSelected" :class="{ 'weui-tabbar__item': true, 'weui-bar__item_on': true }">
		<span v-if="icon" style="display: inline-block;position: relative;">
			<i :class="iconStyle"></i>
		</span>
		<p class="weui-tabbar__label">{{ text }}</p>
	</a>
</template>

<script>
	export default {
		name: 'NavButton',
		props: {
			name: {
				type: String,
				default: ''
			},
			text: {
				type: String,
				default: ''
			},
			icon: {
				type: String,
				default: ''
			},
		},
		computed: {
			iconStyle(){				
				return `weui-tabbar__icon shadow_icon fa fa-2x ${this.icon}`;
			}
		},
		data() {
			return {
				
			};
		},
		methods: {
			onSelected() {
				this.$emit('selected', this.name);
			}
		}
		
	}
</script>

